import React, { useState } from "react";
import { ArrowNarrowRightIcon } from '@heroicons/react/solid';
import data from "../data/personal";
import { NavLink } from "react-router-dom";

const Personal = () => {
  const [items] = useState(data);
  return (
    <div className="container mx-auto p-6 md:px-28 md:py-20">
      <div className="items-center h-auto mb-12">
        <div className="flex gap-2 w-full mb-10">
          <ArrowNarrowRightIcon className="h-6 w-6 text-gray-400 my-auto"></ArrowNarrowRightIcon>
          <h1 className="font-bold text-white text-2xl sm:text-3xl">
            Sobre mí
          </h1>
        </div>
        <p className="text-white font-medium text-lg tracking-wide mb-6">
          Actualmente me dedico a la informática, especializándome en la Administración de Sistemas y en el Desarrollo Web.
          No programo ECUs para coches, ni hackeo cuentas de instagram, pero en mi catálogo de cosas que puedo hacer entra por ejemplo:
          configuración, administración y mantenimiento de sistemas informáticos, y también diseño, desarrollo y mantenimiento de aplicaciones web y más...👨‍💻
        </p>
        <p className="text-white font-medium text-lg tracking-wide mb-6">
          Trabajé en el departamento de informática de ICOT Grupo Sanitario, para la resolución de incidencias de los usuarios tanto presencial como a distancia.
          Durante este tiempo mejoré en la comunicación con usuarios, adaptación a nuevos entornos, y en la resolución de problemas en general.
        </p>
        <p className="text-white font-medium text-lg tracking-wide mb-6">
          Más tarde acabé en una <i>start-up</i> llamada Robottainment, ocupando el rol de desarrollador web full-stack, durante ese tiempo estuve diseñando la web y
          desarrollando tanto el back-end como el front-end. Esta oportunidad me permitió aprender y afianzar muchos conceptos, y además me permitió desarrollarme como autodidacta.
        </p>
        <p className="text-white font-medium text-lg tracking-wide mb-6">
          Si has llegado hasta aquí mereces un cheeto 👇
        </p>
        <NavLink to="/cheeto" exact activeClassName="active">
          <span className="text-malibu font-bold italic rrss">
            Haz click aquí para recibirlo
            </span>
        </NavLink>
      </div>
      <div className="items-center h-auto">
        <div className="flex gap-2 w-full mb-10">
          <ArrowNarrowRightIcon className="h-6 w-6 text-gray-400 my-auto"></ArrowNarrowRightIcon>
          <h1 className="font-bold text-white text-2xl sm:text-3xl">
            Más info.
          </h1>
        </div>
        <table className="table-fixed w-full">
          <tbody className="text-gray-300 font-medium  text-lg tracking-wide">
            {items.map((item) => {
              const { id, title, content } = item;
              return (
                <tr key={id} className="border-b border-shark-400">
                  <td className="p-3 md:w-4/6">{title}</td>
                  <td className="p-3 md:w-2/6">{content}</td>
                </tr>
              );
            })}
          </tbody>
        </table>
      </div>
    </div>
  );
};

export default Personal;
